import { titlePre3, titlePre2, sharedesc1, INVITE_ACTION_GROUP, TITLE_FRIEND_INVITE, TITLE_REDPACK, SHARE_DESC_3, SHARE_DESC_1 } from './../../config';
import { ROOT_PATH, titlePre1 } from '~/config';

/*
 * @Author: qiao 
 * @Date: 2017-12-19 09:20:50 
 * @Last Modified by: qiao
 * @Last Modified time: 2018-02-28 15:01:09
 * @description:  link 页面
 */

import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import floatButton from 'muse-ui/floatButton';
import popup from 'muse-ui/popup';

import { LOADING_TEXT, USER_ROLES, MODAL_0 } from '~/config';
import { ToastMsg } from '~/entity/toast';
import { IUserInterface } from '~/entity/user';
import { IlocalPage } from '~/entity/page';
import { ITask } from '~/entity/task';
import Api from '~/api';
import Utils from '~/utils';
import { Ipage } from '~/entity/page/index';
import { IRecommendParams, IReceivedTaskDetailParams, IRedPacketDetailParams, IPersonalParams } from '~/entity/route_params';
// import Bus from '~/eventBus';
import Cache, { CACHE_NAME } from '~/cache';
import { setTimeout } from 'timers';

@Component({
    components: {
        'mu-float-button': floatButton,
        'mu-popup': popup
    }
})
export default class LinkComp extends Vue {
    
    pageSize: number = 10;

    loadingText = LOADING_TEXT;

    /* 底部modal */
    @State(state => state.modal.MODAL_0)
    isShowModal;
    @State(state => state.user.aliPaymentId)
    alipayId: string;
    @State(state => state.user.roles)
    myRoles: string[];
    @State(state => state.user.user)
    myInfo: IUserInterface;

    isActiveModal = false;

    @Action('updateTabValue')
    updateTabValue;

    @Action('addModal')
    addModal;

    @Action('hideModal')
    hideModal;

    /* 滑动tab配置 */
    swiperOpt: any = new class {
        on: any = {};
        touchAngle = 20;
        iOSEdgeSwipeDetection = true;
        shortSwipes = true;
        constructor(public superThis: LinkComp) {
            this.on.slideChangeTransitionEnd = function () {
                // superThis.initWxByTabIndex('' + this.activeIndex, false); // 初始化分享内容
                superThis.changeTab('' + this.activeIndex, false);
                superThis.setWXShareByTab('' + this.activeIndex);
            };
        }
    }(this);

    activeTabIndex: string = '0';

    /* 悬赏榜(红包列表) */
    matchTaskListContainer: HTMLElement = null;

    refreshingMatchTask = false;

    loadingMatchTask = false;

    matchTaskPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    matchTaskList: ITask[] = [];

    /* 月老簿（我接受的任务列表） */
    receivedTaskListContainer: HTMLElement = null;

    refreshingReceivedTask = false;

    loadingReceivedTask = false;

    receivedTaskPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    receivedTaskList: ITask[] = [];

    /* 红包部分数据 */
    redPackeStatus = 'close';

    // 需要抢的红包任务
    receivingTask: ITask = null;

    // 月老簿邀请码
    receivedTaskInviteCode: string = null;

    @Action('showToast')
    showToast;

    $refs: {
        linkSwiper: any,
        matchTaskListContainer: any,
        receivedTaskListContainer: any
    };

    get swiper() {
        return this.$refs.linkSwiper.swiper;
    }

    mounted() {
        console.log('mounted');
        this.matchTaskListContainer = this.$refs.matchTaskListContainer;
        this.receivedTaskListContainer = this.$refs.receivedTaskListContainer;
    }

    async activated() {
        this.isActiveModal = true;
        this.updateTabValue('link');
        await Utils.initedUser();
        // 刷新悬赏榜
        this.refreshMatchTask();
        // 刷新任务列表
        this.refreshReceivedTask();

        //   this.initWxByTabIndex(this.activeTabIndex, true);
        this.initWX();
    }
    
    deactivated() {
        this.isActiveModal = false;
        // 退出时检查是否关闭模态框
        if (this.isShowModal) {
            this.hideModal();
        }
    }

    /***************微信分享*************** */
    async initWX() {
        // 从月老簿分享出去需要加好友
        this.receivedTaskInviteCode = await Utils.initWX(INVITE_ACTION_GROUP.ADD_FRIEND);
        this.setWXShareByTab(this.activeTabIndex);
    }

    setWXShareByTab(tabVal: string) {
        let url: string;
        let title: string;
        let desc: string;
        let imgUrl: string;
        if (tabVal === '0') {
            // 悬赏榜 分享到 link悬赏榜，无动作
            url = Utils.getShareUrl('/tab/link');
            title = TITLE_REDPACK + this.myInfo.realname;
            desc = SHARE_DESC_3;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        } else {
            // 月老簿 分享到 link悬赏榜，加好友
            url = Utils.getShareUrl('/tab/link', this.receivedTaskInviteCode);
            title = TITLE_FRIEND_INVITE + this.myInfo.realname;
            desc = SHARE_DESC_1;
            imgUrl = Utils.getThumbnailImg(this.myInfo.headPortrait);
        }
        Utils.setWXShareConfig(url, title, desc, imgUrl);
    }

    changeTab(val: string, isSlide: boolean = true) {
        this.activeTabIndex = val;
        if (isSlide) {
            this.swiper.slideTo(parseInt(val, 10));
        }
    }

    /**
     * 检查红包是否可以领，并且为 任务添加一个 enableReceive 字段
     * @param tasks 传入的任务列表
     */
    isRedPacketEnable(tasks: ITask[]) {
        for (const task of tasks) {
			if (task.outOfReceiveTime === 0) {
				task['enableReceive'] = false;
			} else if (task.currentCount === 0) {
				task['enableReceive'] = false;
			} else if (task.subsetTasks.length === 0) {
				task['enableReceive'] = true;
			} else {
				task['enableReceive'] = false;
			}
		}
    }

    /**
     * 点击进入悬赏人主页
     * @param matchTask 
     */
    clickMatchTask(matchTask: ITask) {
        const params: IPersonalParams = {
            action: 'redLine',
            id: matchTask.fromUid
        };
        this['$router'].push({
            name: 'personalPage',
            params
        });
    }
    /**
     * 领取任务红包
     * @param event 
     * @param matchTask 
     */
    receiveTask(event: Event, matchTask: ITask) {

        // if (!matchTask['enableReceive']) return;
        if (matchTask.invalid !== 1) return;

        if (matchTask.fromUid === this.myInfo.uid) {
            this.showToast(new ToastMsg('无法领取自己的红包!'));
            return;
        }
         
        // ******暂时不做认证限制*******
        // 如果是定 公开任务， 则需要 认证后才能抢
        // if (matchTask.taskType === 2 && this.myRoles.indexOf(USER_ROLES.authorized_matchmaker) === -1) {
        //     this.showToast(new ToastMsg('您未认证，暂时无法领取该红包，请先完成认证。'));
        //     // 跳转到验证界面，待增加
        //     this['$router'].push({
        //         name: 'authIndex'
        //     });
        //     return;
        // }

        console.log('打开红包');
        this.receivingTask = matchTask;
        this.addModal(MODAL_0);

        // event.stopPropagation();
    }

    closeModel() {
        this.redPackeStatus = 'close';
        if (this.isShowModal) {
            this.hideModal();
        }
        // history.back();
    }

    /**
     * 打开红包动作
     * @param event 
     */
    async openRedPacket(event: Event) {
        console.log('开红包');
        this.redPackeStatus = 'opening';

        const target = <HTMLElement> event.target;
        target.classList.add('rotate');

        // 发出抢红包请求
        let isSuccess = false;
        try {
            const res = await Api.receiveTask(this.receivingTask.taskId, this.receivingTask.taskType);
            // 保存红包任务详情，到详情页需要使用
            Utils.saveItem(CACHE_NAME.redpacketDetail + this.receivingTask.taskId, res.body.obj);
            // Cache.set(CACHE_NAME.redpacketDetail + this.receivingTask.taskId, res.body.obj);         

            isSuccess = true;
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            setTimeout(() => {
                target.classList.remove('rotate');
                this.redPackeStatus = 'close';
                if (isSuccess) {
                    // 设置当前任务不可再抢
                    this.receivingTask['enableReceive'] = false;
                    // 跳转到红包详情页
                    this.closeModel();
                    console.log('红包详情');
                    setTimeout(() => {
                        const params: IRedPacketDetailParams = {
                            taskId: this.receivingTask.taskId
                        };
                        // 测试语句
                        // Cache.set(CACHE_NAME.redpacketDetail + this.receivingTask.taskId, this.receivingTask);   
                        this['$router'].push({
                            name: 'redpackDetail',
                            params
                        });
                    }, 20);
                }
            }, 1000); 
        }
    }

    /**
     * 刷新悬赏榜
     */
    async refreshMatchTask() {
        this.refreshingMatchTask = true;
        try {
            // 刷新
            const res = await Api.getMatchTasks(1, this.pageSize);
            this.matchTaskList = res.body.obj.rows;
            // this.isRedPacketEnable(this.matchTaskList);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.matchTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingMatchTask = false;
        }
    }

    /**
     * 加载更多悬赏榜
     */
    async loadMoreMatchTask() {
        this.loadingMatchTask = true;
        try {
            const res = await Api.getMatchTasks(this.matchTaskPage.curPage + 1, this.pageSize);
            const matchTasks: ITask[] = res.body.obj.rows;
            // this.isRedPacketEnable(matchTasks);
            this.matchTaskList = this.matchTaskList.concat(matchTasks);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.matchTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.matchTaskPage.hasMore = false;
        } finally {
            this.loadingMatchTask = false;
        }
    }

    /**
     * 刷新接受任务列表
     */
    async refreshReceivedTask() {
        this.refreshingReceivedTask = true;
        try {
            const res = await Api.getReceivedTasks(1, this.pageSize);
            this.receivedTaskList = res.body.obj.rows;
            
            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.receivedTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingReceivedTask = false;
        }
    }

    /**
     * 加载更多接受任务列表
     */
    async loadMoreReceivedTask() {
        this.loadingReceivedTask = true;
        try {
            const res = await Api.getReceivedTasks(this.receivedTaskPage.curPage + 1, this.pageSize);
            const receivedTasks: ITask[] = res.body.obj.rows;
            this.receivedTaskList = this.receivedTaskList.concat(receivedTasks);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.receivedTaskPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.receivedTaskPage.hasMore = false;
        } finally {
            this.loadingReceivedTask = false;
        }
    }

    /**
     * 进入推荐页面
     * @param event 
     * @param task 
     */
    toRecommendPage(event: Event, task: ITask) {
    // 判断任务是否正在进行
        if (task.subsetTasks[0].taskStatus === 0) {
            const params: IRecommendParams = {
                action: 'task',
                uid: task.fromUid,
                subId: task.subsetTasks[0].subId
            };
            this['$router'].push({
                name: 'recommend',
                params
            });
        }
        event.stopPropagation();
    }

    clickReceivedTask(event: Event, task: ITask) {
        const target = <HTMLElement>event.target;
        const action = target.getAttribute('data-action');
        if (action === 'userPage') {
            const params: IPersonalParams = {
                action: 'otherUser',
                id: task.fromUid
            };
            this['$router'].push({
                name: 'personalPage',
                params
            });
        } else {
            const params: IReceivedTaskDetailParams = {
                taskId: task.taskId
            };
            this['$router'].push({
                name: 'receivedTaskDetail',
                params
            });
        }
    }

    /**
     * 领取任务金额
     * @param event 
     * @param task 
     */
    async clearTask(event: Event, task: ITask) {
        const clearingStatus = task.subsetTasks[0].clearingStatus;
        if (clearingStatus === 0 || clearingStatus === -1) { // 金额未结算 或者 结算失败
            if (this.alipayId === null) {
                this.showToast(new ToastMsg('您未绑定支付宝账户，请先绑定支付宝账户。'));            
                this['$router'].push({
                    name: 'bindAlipay'
                });
                return;
            }
            if (this.myRoles.indexOf(USER_ROLES.authorized_matchmaker) === -1) {
                this.showToast(new ToastMsg('您未认证，暂时无法领取红包，请先完成认证。'));
                // 跳转到验证界面，待增加
                this['$router'].push({
                    name: 'authIndex'
                });
                return;
            }
            try {
                const res = await Api.clearTask(task.subsetTasks[0].subId, 1);
                this.showToast(new ToastMsg('申请结算成功，红包将在24小时内发放至您的支付宝账户！'));
            } catch (e) {
                this.showToast(new ToastMsg(e.message));
            }            
        } else if (clearingStatus === 1) {
            this.showToast(new ToastMsg('金额结算中'));
        } else {
            this.showToast(new ToastMsg('金额已结算'));
        }
        event.stopPropagation();
    }
}



